import React, { Component } from 'react';
import Calendar from 'react-calendar';
import Clock from '../components/Clock';

class HomePage extends Component {
	constructor(props) {
		super(props);
		this.state = {
			date: new Date()
		};
	}
	onChange = (date) => this.setState({ date });

	render() {
		// these are the settings for the digital clock
		const type = "digital", 
					size = "sm", 
					color = "rgb(180,254,115)", // "#E41400", 
					bg = "#3c4144ea", 
					stroke = "#323639"
		return (
			<div className="container">
				<div className="row">
					<div className="col">
						<Clock type={type} size={size} color={color} bg={bg} stroke={stroke} />
						<div className="clock-white" style={{ fontSize: '26px' }}>
							{/* <Clock type="date" color={color}/> */}
						</div>
					</div>
					<div className="col mt-3 mb-3">
						<Calendar value={this.state.date} view="month" />
					</div>
				</div>
			</div>
		);
	}
}
export default HomePage;
