import React, { Component } from 'react';
import Digits from './Digits';

class Clock extends Component {
	constructor(props) {
		super(props);
		this.state = { date: new Date() };
	}

	// myTimer = () => {
	//     //console.log(Date.now)
	//     this.setState({ date: new Date() },)
	//     clearInterval(this.updateTime)
	// }
	componentDidMount = () => {
		if (!this.tickTimer) {
			this.tickTimer = setInterval(() => {
				this.setState({ date: new Date() });
			}, 1000);
		}
	};

	componentWillUnmount = () => {
		if (this.tickTimer) {
			clearInterval(this.tickTimer);
		}
	};

	num1 = () => {
		document.createElement('svg');
	};
	//num1.appendChild("polygon");

	render() {
		const months = [
			'January',
			'February',
			'March',
			'April',
			'May',
			'June',
			'July',
			'August',
			'September',
			'October',
			'November',
			'December'
		];
		const weekdays = ['Sunday', 'Monday', 'Tuesday', 'Wednesday', 'Thursday', 'Friday', 'Saturday'];
		const myDate = this.state.date;
		const monthName = months[myDate.getMonth()];
		const monthVal = myDate.getMonth();
		const weekday = weekdays[myDate.getDay()];
		const day = myDate.getDate();
		const year = myDate.getFullYear();
		const hour = (myDate.getHours() % 12 > 0 && myDate.getHours() % 12 < 10) ? `0${myDate.getHours() % 12}` : `${myDate.getHours() === '12'}` ? `${myDate.getHours()}` : `${myDate.getHours() % 12}`;
		const min = myDate.getMinutes() < 10 ? `0${myDate.getMinutes()}` : `${myDate.getMinutes()}`;
		const sec = myDate.getSeconds() < 10 ? `0${myDate.getSeconds()}` : `${myDate.getSeconds()}`;
		const ampm = myDate.getHours() < 12 ? 'AM' : 'PM';
		const hourStr = hour.split('')
		const minStr = min.split('');
		// const secStr = sec.split('');
		const hour_1 = hourStr[0];
		const hour_2 = hourStr[1];
		const min_1 = minStr[0];
		const min_2 = minStr[1];
		// const sec_1 = secStr[0];
		// const sec_2 = secStr[1];

		if (this.props.type === 'time') {
			return (
				<div className="clock-white float-right">
					{hour}
					<span className={`${sec % 2 === 0 ? 'clock-white' : 'clock-dark-blink'}`}>:</span>
					{min}
					<span className='clock-white'>{`${ampm}` === "AM" ? " AM" : " PM"}</span>
				</div>
			);
		} else if (this.props.type === 'date') {
			return (
				<div className="card-body float-right" style={{ "color": this.props.color }}>
					{weekday} - {monthName} {day}
				</div>
			);
		} else if (this.props.type === 'short-date') {
			return (
				<div className="card-body float-right" style={{ "color": this.props.color }}>
					{monthVal}/{day}/{year}
				</div>
			);

		} else if (this.props.type === 'digital') {
			return (
				<div className="container mt-3 ">
					<div className="row align-items-center">
						<div className="col-lg">
							{/* Hours  */}
							<Digits
								num={`${hour_1}`}
								size={this.props.size}
								color={this.props.color}
								bg={this.props.bg}
								stroke={this.props.stroke}
							/>
							<Digits
								num={`${hour_2}`}
								size={this.props.size}
								color={this.props.color}
								bg={this.props.bg}
								stroke={this.props.stroke}
							/>
							{/* Divider  */}
							<Digits
								num={`${sec % 2 === 0 ? 'divider-light' : 'divider-dark'}`}
								color={this.props.color}
								bg={this.props.bg}
								stroke={this.props.stroke}
							/>
							<Digits
								num={`${min_1}`}
								size={this.props.size}
								color={this.props.color}
								bg={this.props.bg}
								stroke={this.props.stroke}
							/>
							{/* Minutes */}
							<Digits
								num={`${min_2}`}
								size={this.props.size}
								color={this.props.color}
								bg={this.props.bg}
								stroke={this.props.stroke}
							/>
							{/* Seconds */}
							{/* <Digits num={`${sec % 2 === 0 ? 'divider-light' : 'divider-dark'}`} color={this.props.color} bg={this.props.bg} stroke={this.props.stroke} />
							<Digits num={`${sec_1}`} size={this.props.size} color={this.props.color} bg={this.props.bg} stroke={this.props.stroke} />
							<Digits num={`${sec_2}`} size={this.props.size} color={this.props.color} bg={this.props.bg} stroke={this.props.stroke} /> */}

							<Digits
								num={ampm === 'AM' ? 'am' : 'pm'}
								size={this.props.size}
								color={this.props.color}
								bg={this.props.bg}
								stroke={this.props.stroke}
							/>
						</div>
					</div>
				</div>
			);
		}
	}
}
export default Clock;
